<template>
  <div class="login">
    <h1 style="margin-bottom:20px;">JMQTT-web-console</h1>
    <Row>
      <Col span="12" offset="6">
        <Input
          type="password"
          v-model="form.web_console_token"
          enter-button="登录"
          placeholder="输入登录口令"
        />
      </Col>
    </Row>
    <Row class="login-btn">
      <Button @click="login" long type="primary">登录</Button>
    </Row>
  </div>
</template>

<script>
let config = require("../config/config.json");
export default {
  data() {
    return {
      form: {
        web_console_token: "qsedftr45g656hbtreQ#Q$RWDQ"
      },
      ruleInline: {}
    };
  },
  methods: {
    /**
    http://localhost:2580/dashboard/login
     */
    login() {
      let _this = this;
      console.log("登录:", this.form.web_console_token);
      this.axios
        .post(
          "http://" +
            config.host +
            ":" +
            config.port +
            config.context +
            "dashboard/login",
          _this.form
        )
        .then(response => {
          console.log(response.data);
          if (response.data.state == 1) {
            this.$Message.success("登陆成功");
            localStorage.setItem("web_console_token", response.data.token);
            this.$router.push('/dashboard');
          } else {
            this.$Message.error("登陆失败");
          }
        })
        .catch(response => {
          console.log(response);
        });
    }
  }
};
</script>
<style scoped>
.login {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 200px;
}
.login-btn {
  width: 40%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
}
</style>
